@charset "UTF-8";
@media screen and (max-width: 480px) {
  body {
    font-family: "メイリオ", Verdana, sans-serif;
    background: black;
    color: white;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
    width: 480px;
    position: relative;
  }
  .main {
    display: none;
  }
  .main_container {
    display: none;
  }
  .main_rapper_Aria {
    display: none;
  }
  .main_480px {
    width: 100%;
    height: 300px;
    background: red;
  }
  .main_slidshow_480px1 {
    position: absolute;
    background: url("../images/dbd_mainMin.jpg");
    background-size: cover;
    width: 100%;
    height: 300px;
    transition: 0.8s;
    z-index: 4;
  }
  .main_slidshow_480px2 {
    position: absolute;
    background: url("../images/dbd_slid1Min.jpg");
    background-size: cover;
    width: 100%;
    height: 300px;
    z-index: 3;
    transition: 0.8s;
  }
  .main_slidshow_480px3 {
    position: absolute;
    background: url("../images/dbd_slid2Min.jpg");
    background-size: cover;
    width: 100%;
    height: 300px;
    right: 0;
    z-index: 2;
    transition: 0.8s;
  }
  .main_slidshow_480px4 {
    position: absolute;
    background: url("../images/dbd_slid3Min.jpg");
    background-size: cover;
    width: 100%;
    height: 300px;
    z-index: 1;
    transition: 0.8s;
  }
  span.main_message {
    display: block;
    margin: 0 auto;
    font-size: 24px;
    text-align: center;
  }
}